// –ü–æ–ª—ñ—Ñ—ñ–ª –¥–ª—è –≥–ª–æ–±–∞–ª—å–Ω–æ—ó –∑–º—ñ–Ω–Ω–æ—ó (–≤–∏—Ä—ñ—à—É—î "global is not defined" —É –±—Ä–∞—É–∑–µ—Ä—ñ)
window.global = window;

import 'izitoast/dist/css/iziToast.min.css';
import iziToast from 'izitoast';

import { getImagesByQuery } from './js/pixabay-api.js';
import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from './js/render-functions.js';
import './css/styles.css';

const PER_PAGE = 15;

// –ü—Ä–∞–≤–∏–ª—å–Ω—ñ —Å–µ–ª–µ–∫—Ç–æ—Ä–∏ –∑–≥—ñ–¥–Ω–æ –∑ –Ω–æ–≤–∏–º HTML
const formEl = document.querySelector('.form');
const galleryEl = document.querySelector('.gallery');
const loadMoreBtnEl = document.querySelector('.load-more');
const loaderEl = document.querySelector('.loader');

let currentQuery = '';
let currentPage = 1;
let totalPages = 0;

// –ü–æ–¥—ñ—ó
formEl.addEventListener('submit', onSearchSubmit);
loadMoreBtnEl.addEventListener('click', onLoadMore);

// –ü–æ—à—É–∫ –∑–æ–±—Ä–∞–∂–µ–Ω—å
async function onSearchSubmit(e) {
  e.preventDefault();

  const query = e.target.query.value.trim();
  if (!query) {
    iziToast.warning({
      title: '–£–≤–∞–≥–∞',
      message: '–í–≤–µ–¥—ñ—Ç—å –ø–æ—à—É–∫–æ–≤–∏–π –∑–∞–ø–∏—Ç',
      position: 'topRight',
    });
    return;
  }

  currentQuery = query;
  currentPage = 1;
  clearGallery();
  hideLoadMoreButton();
  showLoader(loaderEl);

  try {
    const { hits, totalHits } = await getImagesByQuery(currentQuery, currentPage);

    if (hits.length === 0) {
      iziToast.info({
        title: '–ù—ñ—á–æ–≥–æ –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ',
        message: '–°–ø—Ä–æ–±—É–π—Ç–µ —ñ–Ω—à–∏–π –∑–∞–ø–∏—Ç',
        position: 'topRight',
      });
      return;
    }

    createGallery(hits);
    totalPages = Math.ceil(totalHits / PER_PAGE);

    if (currentPage < totalPages) {
      showLoadMoreButton();
    } else {
      iziToast.info({
        message: "–ú–∏ –≤–∏–±–∞—á–∞—î–º–æ—Å—è, –∞–ª–µ –≤–∏ –¥–æ—Å—è–≥–ª–∏ –∫—ñ–Ω—Ü—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤ –ø–æ—à—É–∫—É.",
        position: 'topRight',
      });
    }
  } catch {
    iziToast.error({
      title: '–ü–æ–º–∏–ª–∫–∞',
      message: '–ù–µ –≤–¥–∞–ª–æ—Å—è –æ—Ç—Ä–∏–º–∞—Ç–∏ –¥–∞–Ω—ñ –∑ Pixabay',
      position: 'topRight',
    });
  } finally {
    hideLoader(loaderEl);
    e.target.reset();
  }
}

// –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –¥–æ–¥–∞—Ç–∫–æ–≤–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å
async function onLoadMore() {
  currentPage += 1;
  showLoader(loaderEl);

  try {
    const { hits } = await getImagesByQuery(currentQuery, currentPage);
    createGallery(hits);
    smoothScroll();

    if (currentPage >= totalPages) {
      hideLoadMoreButton();
      iziToast.info({
        message: "–ú–∏ –≤–∏–±–∞—á–∞—î–º–æ—Å—è, –∞–ª–µ –≤–∏ –¥–æ—Å—è–≥–ª–∏ –∫—ñ–Ω—Ü—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤ –ø–æ—à—É–∫—É.",
        position: 'topRight',
      });
    }
  } catch {
    iziToast.error({
      title: '–ü–æ–º–∏–ª–∫–∞',
      message: '–ù–µ –≤–¥–∞–ª–æ—Å—è –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ –±—ñ–ª—å—à–µ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤',
      position: 'topRight',
    });
  } finally {
    hideLoader(loaderEl);
  }
}

// –ü–ª–∞–≤–Ω–∏–π —Å–∫—Ä–æ–ª –ø—ñ—Å–ª—è –¥–æ–¥–∞–≤–∞–Ω–Ω—è –Ω–æ–≤–∏—Ö –∫–∞—Ä—Ç–æ–∫
function smoothScroll() {
  const firstCard = galleryEl.firstElementChild;
  if (!firstCard) return;
  const { height } = firstCard.getBoundingClientRect();
  window.scrollBy({ top: height * 2, behavior: 'smooth' });
}