// –§—É–Ω–∫—Ü—ñ—ó UI-—Ä–µ–Ω–¥–µ—Ä—É —Ç–∞ –∫–µ—Ä—É–≤–∞–Ω–Ω—è –≤–∏–¥–∏–º—ñ—Å—Ç—é –µ–ª–µ–º–µ–Ω—Ç—ñ–≤ + SimpleLightbox
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

const galleryEl = document.querySelector('.gallery');
const loadMoreBtnEl = document.querySelector('.load-more');
const loaderEl = document.querySelector('.loader');

const lightbox = new SimpleLightbox('.gallery a', {
  captions: true,
  captionsData: 'alt',
  captionDelay: 250,
});

/**
 * –°—Ç–≤–æ—Ä—é—î —Ç–∞ –¥–æ–¥–∞—î —Ä–æ–∑–º—ñ—Ç–∫—É –∫–∞—Ä—Ç–æ–∫ –≥–∞–ª–µ—Ä–µ—ó –∑–∞ –æ–¥–Ω—É –æ–ø–µ—Ä–∞—Ü—ñ—é.
 * @param {Array<Object>} images
 */
export function createGallery(images) {
  if (!Array.isArray(images) || images.length === 0) return;

  const markup = images
    .map(
      ({
        largeImageURL,
        webformatURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => `
      <li class="photo-card">
        <a href="${largeImageURL}">
          <img src="${webformatURL}" alt="${escapeHtml(tags)}" loading="lazy" />
        </a>
        <div class="info">
          <div class="info-item">
            <b>Likes</b>
            <span>${likes ?? 0}</span>
          </div>
          <div class="info-item">
            <b>Views</b>
            <span>${views ?? 0}</span>
          </div>
          <div class="info-item">
            <b>Comments</b>
            <span>${comments ?? 0}</span>
          </div>
          <div class="info-item">
            <b>Downloads</b>
            <span>${downloads ?? 0}</span>
          </div>
        </div>
      </li>`
    )
    .join('');

  galleryEl.insertAdjacentHTML('beforeend', markup);
  lightbox.refresh();
}

/** –û—á–∏—â–∞—î –≥–∞–ª–µ—Ä–µ—é */
export function clearGallery() {
  galleryEl.innerHTML = '';
}

/** –ü–æ–∫–∞–∑–∞—Ç–∏ –ª–æ–∞–¥–µ—Ä */
export function showLoader() {
  loaderEl.classList.remove('is-hidden');
  loaderEl.setAttribute('aria-hidden', 'false');
}

/** –ü—Ä–∏—Ö–æ–≤–∞—Ç–∏ –ª–æ–∞–¥–µ—Ä */
export function hideLoader() {
  loaderEl.classList.add('is-hidden');
  loaderEl.setAttribute('aria-hidden', 'true');
}

/** –ü–æ–∫–∞–∑–∞—Ç–∏ –∫–Ω–æ–ø–∫—É Load more */
export function showLoadMoreButton() {
  loadMoreBtnEl.classList.remove('is-hidden');
}

/** –ü—Ä–∏—Ö–æ–≤–∞—Ç–∏ –∫–Ω–æ–ø–∫—É Load more */
export function hideLoadMoreButton() {
  loadMoreBtnEl.classList.add('is-hidden');
}

/** –ï–ª–µ–º–µ–Ω—Ç –≥–∞–ª–µ—Ä–µ—ó (–¥–ª—è —Å–∫—Ä–æ–ª—É) */
export function getGalleryElement() {
  return galleryEl;
}

/** –ö–Ω–æ–ø–∫–∞ Load more (–¥–ª—è –æ–±—Ä–æ–±–Ω–∏–∫–∞) */
export function getLoadMoreButton() {
  return loadMoreBtnEl;
}

/* –í—Ç–µ—á–∞ —Å–ø–µ—Ü—Å–∏–º–≤–æ–ª—ñ–≤ —É alt/—Ç–µ–∫—Å—Ç–∞—Ö */
function escapeHtml(str = '') {
  return String(str)
    .replaceAll('&', '&amp;')
    .replaceAll('<', '&lt;')
    .replaceAll('>', '&gt;')
    .replaceAll('"', '&quot;')
    .replaceAll("'", '&#39;');
}